body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg_card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  margin: 30px auto;
  width: 90%;
  height: 90vh;
  border-radius: 10px;
}

.bg_card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.container {
  padding: 50px 16px;
}

.left {
  float: left;
}
.left h4{
  padding-top: 8px;
  padding-right: 10px;
  font-size: large;
}
.right {
  float: right;
  height: 30vh;
  width: 70%;
}
.chart{
  height: 30vh;
  width: 90%;
  margin: 50px auto;
}
.temp{
  float: left;
  font-size: x-large;
  font-weight: lighter;
  margin-left: 50px;
}

.weather{
  margin: 20% 10%;
}
.degree{
  margin: 10%;
  font-size: 60px;
  font-weight: bold;
}

.left .time{
  margin-top: 50px;
  font-size: large;
  color: gray;
  letter-spacing: 3px;
}
.weather_name{
  text-align: center;
  font-size: 25px;
  font-weight: bold;
}
.weather_description{
  margin: 10%;
  font-size: 20px;
}
.weather_description h4{
  font-weight: lighter;
  color: gray;
}

.img-fluid{
  width: 40%;
  height: 20%;
  float: left;
  animation: fadeIn 5s;
  -webkit-animation: fadeIn 5s;
  -moz-animation: fadeIn 5s;
  -o-animation: fadeIn 5s;
  -ms-animation: fadeIn 5s;
}

.row img{
  width:35%;
  align: center;
}
.row{
  padding: 30px;
  width: 250px;
}


.fade-in-image {
  animation: fadeIn 5s;
  -webkit-animation: fadeIn 5s;
  -moz-animation: fadeIn 5s;
  -o-animation: fadeIn 5s;
  -ms-animation: fadeIn 5s;
}
@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-moz-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-o-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-ms-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}